.about__paragraph {
    font-size: 24px;
    text-align: center;
}

.about__paragraph-optica,
.about__paragraph-founder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.about__items-optica,
.about__items-founder,
.about__paragraph-doctor {
    overflow: hidden;
    display: flex;
    align-items: center;
    margin:40px 0;
}

.about__paragraph-doctor {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
}

@media screen and (min-width:320px) and (max-width:767px) {
    .about__items-optica,
    .about__items-founder {
        flex-direction: column;
        align-items: center;
        justify-content: center;

        & li:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    .about__paragraph-doctor {
        flex-direction: column;
    }

    .about__items-doctor {
        margin-bottom: 40px;
    }
}

@media screen and (min-width:768px) {
    .about__items-optica,
    .about__items-founder {
        width: 704px;
        justify-content: space-between;
    }

    .about__paragraph-doctor {
        flex-direction: row;    
    }

    .about__items-doctor {
        margin-right: 40px;
    }

    .about__founder-img {
        height: 285px;
        object-fit: cover;
    }
}

@media screen and (min-width:1024px) {
    .about__items-optica,
    .about__items-founder {
        width: 960px;
        justify-content: space-between;
    }
}

@media screen and (min-width:1200px) {
    .about__items-optica,
    .about__items-founder {
        width: 1136px;
        justify-content: space-between;
    }
}

.about__img-optica, 
.about__img-doctor,
.about__founder-img {
    object-fit: cover;
    display: block;
    border-radius: 10px;

    @media screen and (min-width:320px) and (max-width:375px) {
        width: 280px;
    }
        
    @media screen and (min-width:375px) and (max-width:480px) {
        width: 345px;
    }
        
    @media screen and (min-width:480px) and (max-width:767px) {
        width: 450px;
    }

    @media screen and (min-width:320px) and (max-width:767px) {
        height: auto;
    }

    @media screen and (min-width:768px) and (max-width:1023px) {
        width: 220px;
    }

    @media screen and (min-width:1024px) and (max-width:1200px) {
       width: 320px;
    }

    @media screen and (min-width:1024px) and (max-width:1200px) {
        width: 300px;
    }

    @media screen and (min-width:1200px) {
        width: auto;
    }
}

.about__paragraph-last {
    text-align: center;
    font-family: "Cormorant Infant";
    font-size: 48px;
    font-weight: 900;
    margin-bottom: 40px;
}