  align-items: center;
  width: 130px;
  height: 130px;
  padding: 14px;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.logo:hover {
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
  box-shadow: -1px -1px 49px 3px #4dc274bf;
}

