  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  box-shadow: -1px -1px 49px 3px #4dc274bf;
}

.header__social-item:hover .header__svg {
  fill: var(--accent-color);
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
}

.header__svg {
  fill: currentColor;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
}

